import React, {useState, useEffect} from 'react'
import Title from '../../common/Title';
import NoticeItem from './NoticeItem';
import List from '@material-ui/core/List';
import { makeStyles } from '@material-ui/core/styles';
import { db } from "../../firebase";

// 

// style
const useStyles = makeStyles((theme) => ({
    root: {
        width: '100%',
        maxWidth: '500px',
        backgroundColor: theme.palette.background.paper,
        margin: "0 auto",
      },
    margin:{
        marginBottom: 60,
    },
  }));

const Notice = ({myId, mypic, mynickname, history}) => {
    const classes = useStyles();

       // firebaseuseState
       const [notice, setNotice] = useState([
        {
            id: "",
            escort_id: "",
            user_id: "",
            icon:"",
            notice: "",
            already_read: "",
            created_at: null,
        },
        ]);
    
    // noticesuser_id=myID
    // dbuser_idmyId
    useEffect(() => {
        const firebaseData = db
            .collection("notices")
            .where("user_id", "==", myId)
            .orderBy("created_at", "desc")
            .onSnapshot((snapshot) =>
                setNotice(
                  snapshot.docs.map((doc) => ({
                    // firebasedoc
                    // post
                    id: doc.id,
                    escort_id: doc.data().escort_id,
                    user_id: doc.data().user_id,
                    icon: doc.data().icon,
                    notice: doc.data().notice,
                    already_read: doc.data().already_read,
                    created_at: doc.data().created_at,
                  }))
                )
              );
        //   
        return () => {
            firebaseData();
            };
        }, []);
    return (
        <>
        <Title title="" history={history}/>
        <div className={classes.margin}></div>   
        <List className={classes.root}>
        {/* noticeitem */}
        {notice && notice.map((item) => (
            // 
            <NoticeItem
                key={item.id}
                id={item.id}
                escort_id={item.escort_id}
                notice={item.notice}
                date={item.date}
                already_read={item.already_read ===0 ? ("colored"):("")}
                icon={item.icon}
                history={history}
                myId={myId}
            />
            ))}
        </List>          
        </>
    )
}

export default Notice

