import React from 'react'
import Typography from '@material-ui/core/Typography';
import Img from "../../img/noimage.jpg";
import { makeStyles } from '@material-ui/core/styles';
import { Button } from '@material-ui/core';
import Map from "./Map";
import { withRouter } from 'react-router-dom';
import Rikkouho from "./Rikkouho";
import RikkouhoCansel from "./RikkouhoCansel";
import Touchaku from "./Touchaku";
import Kanryou from "./Kanryou";

// detail
// style
const useStyles = makeStyles((theme) => ({
    box: {
        padding: theme.spacing(1),
        margin: 'auto',
        maxWidth: 500,
        position: "relative",
      },
    button:{
        boxShadow: "none",
        pointerEvents: "none",
        position: "absolute",
        top: 0,
        right: 5,
    },
    image: {
        width: "6rem",
        height: "6rem",
      },
    img: {
        margin: 'auto',
        display: 'block',
        width: '100%',
        height: '100%',
        objectFit: "cover",
      },
    guestList:{
        display: "flex",
    },
    guestInfo:{
        paddingLeft: "10px",
    },
    guest:{
        paddingTop: 10,
        paddingBottom: 10,
    },
    button2:{
        marginLeft: 10,
    },
    margin:{
        marginBottom: 70,
    },
  }));

const DetailItem = ({
    id, picture, nickname, guest_id, rikkouho_id, es_date, start_time,
    departure, departure_lat, departure_lon, destination, destination_lat, destination_lon,               
    number, lan_1, lan_2, lan_3, status, status_2, emergency, history, myId, mynickname, mypic}) => {

    const classes = useStyles();
    
    //
    const gotoGuest=()=>{
        history.push({
            // detailid
            pathname: '/volunteer/guestprofile',
            state: { guestId: guest_id }
        });
    }

    // 
    const gotoForum=()=>{
        history.push({
            //forum
            pathname: '/forum',
            state: {escortId: id,
                    myId: myId,
                    }
        })
    }

    //
    const gotoChat=()=>{
        history.push({
            //forum
            pathname: '/chat',
            state: {escortId: id,
                    myId: myId,
                    }
        })
    }
    
    // rikkouho_id
    let rikkouho ="";
    if (rikkouho_id) {
        rikkouho = rikkouho_id.includes(myId);
    }if(!rikkouho_id){
        rikkouho = false;
    };

    return (
        <>
        <div className={classes.margin}></div>
        <div className={classes.box}>
            <Typography variant="h6" className={classes.title}>
                {nickname}
            </Typography>

            { status==="" ? (
                <>
                  <Button variant="contained" color="primary" className={classes.button} size="large">
                      {status}
                  </Button>
                </>):(
                <>
                <Button variant="outlined" color="primary" className={classes.button} size="large">
                    {status}
                </Button>
                </>)}

            <Typography variant="subtitle2" display="block">
            {emergency===1 ? (``):(`${es_date} ${start_time}`)}
            </Typography>
             
            <Typography variant="subtitle2" display="block">
            
            </Typography>
            <Typography variant="body2" display="block">
            {departure} 
            </Typography>
            <Typography variant="body2" display="block">
            {destination}
            </Typography>

            {/* map */}
            <Map
                departure_lat={departure_lat}
                departure_lon={departure_lon}
                destination_lat={destination_lat}
                destination_lon={destination_lon}
            />
            <div className={classes.guest}>
            <Typography variant="subtitle1" display="block">
            
            </Typography>
            <div className={classes.guestList}>
            <div>
            { picture ? (
                <>
                <div className={classes.image}>
                    <img src={picture} alt={`${nickname}`} className={classes.img}/>
                </div>
                </>
                ) : (
                <>
                <div className={classes.image}>
                    <img src={Img} alt="" className={classes.img}/>
                </div>
                </>
                )
            }
            </div>
            <div className={classes.guestInfo}>
            <Typography variant="body2" display="block">
            {nickname}
            </Typography>
            <Typography variant="body2" display="block">
            {lan_1}{ lan_2 && `${lan_2}`}{ lan_3 && `${lan_3}`}
            </Typography>
            <Typography variant="body2" display="block">
            {number}
            </Typography>
            <Button variant="outlined" color="primary" size="small" onClick={gotoGuest}>
                
            </Button>
            {status === "" &&
            <Button variant="contained" color="primary" size="small" onClick={gotoForum} className={classes.button2}>
                
            </Button>
            }
            {status === "" &&
            <Button variant="contained" color="primary" size="small" onClick={gotoChat} className={classes.button2}>
                
            </Button>
            }
            </div>
            </div>
            </div>

            {/*  */}
            {/*  */}
            { status === "" && rikkouho === false && 
            <Rikkouho 
                id={id} 
                guest_id={guest_id}
                myId={myId}
                mynickname={mynickname}
                mypic={mypic} 
                rikkouho_id={rikkouho_id}
                />
            }

            {/*  */}
            { status === "" && rikkouho === true && 
            <RikkouhoCansel 
                id={id} 
                guest_id={guest_id}
                myId={myId}
                mynickname={mynickname}
                mypic={mypic}
                rikkouho_id={rikkouho_id} />
            }

            {/*  */}
            { status === "" && status_2 === "" &&
            <Touchaku 
                id={id} 
                guest_id={guest_id}
                myId={myId}
                mynickname={mynickname}
                mypic={mypic} />
            }

            {/*  */}
            { status === "" && status_2 === "" &&
            <Kanryou 
                id={id} 
                guest_id={guest_id}
                myId={myId}
                mynickname={mynickname}
                mypic={mypic} />
            }
        </div>
        </>
    )
}

export default withRouter(DetailItem)
